  text-transform: uppercase;
  font-weight: bold;
  letter-spacing: 2px;
}
.Slider__info {
  position: absolute;
  top: 20px;
  left: 20px;
  width: 400px;
  -webkit-transition: opacity 0.3s ease-in-out;
  transition: opacity 0.3s ease-in-out;
}
.Slider__info--bottom {
  color: #fff;
