.diet__list li {
  width: calc(99.99% * 1/4 - (30px - 30px * 1/4));
}
.diet__list li:nth-child(1n){
  float: left;
  margin-right: 30px;
  clear: none;
}
.diet__list li:last-child{
  margin-right: 0;
}
.diet__list li:nth-child(4n){
  margin-right: 0;
  float: right;
}
.diet__list li:nth-child(4n + 1){
  clear: left;
}
